#login-register-form {
    .modal-content {
        margin-top: 150px;
        margin-bottom: 50px;
    }

    .modal-header {
        padding: 0;
        justify-content: center;
        align-items: center;
        background-color: #3270fc;

        .modal-logo {
            display: flex;
            justify-content: center;
            align-items: center;
            height: 40px;

            img {
                width: auto;
                height: 100%;
            }
        }

        .deco {
            padding: 50px 30px;
            width: 100%;
            height: 100%;
            min-height: 151px;
            position: relative;
            overflow: hidden;

            .modal-header-bg {
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                height: 270px;
                background-image: url(https://homeradar.cththemes.co/wp-content/uploads/2021/01/city-bg.png);
                background-repeat: no-repeat;
                background-position: 0% 0%;
                background-size: auto;
                background-attachment: scroll;
                background-origin: content-box;
                opacity: 0.4;
            }

            .deco-1 {
                position: absolute;
                left: 230px;
                top: 170px;
                width: 6px;
                height: 6px;
                border-radius: 100%;
                background: #fff;

                &:before {
                    content: "";
                    position: absolute;
                    left: 0;
                    top: 0;
                    width: 6px;
                    height: 6px;
                    background: #fff;
                    border-radius: 50%;
                    opacity: 0.4;
                    transform: scale(10);
                }
            }

            .deco-2 {
                position: absolute;
                width: 6px;
                height: 6px;
                border-radius: 100%;
                background: #fff;
                left: 70px;
                top: 100px;

                &:before {
                    content: "";
                    position: absolute;
                    left: 0;
                    top: 0;
                    width: 6px;
                    height: 6px;
                    background: #fff;
                    border-radius: 50%;
                    opacity: 0.4;
                    transform: scale(10);
                }
            }

            .deco-3 {
                position: absolute;
                width: 250px;
                height: 250px;
                border-radius: 100%;
                background: rgba(255, 255, 255, 0.1);
                top: -60px;
                left: -130px;
            }

            .deco-4 {
                position: absolute;
                top: -90px;
                right: -50px;
                width: 150px;
                height: 150px;
                border-radius: 100%;
                background: rgba(255, 255, 255, 0.1);
            }
        }

        .close {
            position: absolute;
            top: 20px;
            right: 30px;
            color: #fff;
            font-size: 1rem;
            z-index: 1;
        }
    }

    .modal-body {
        padding-top: 40px;
        padding-bottom: 20px;
        padding-left: 0;
        padding-right: 0;

        .tabs-menu {
            display: flex;
            justify-content: space-between;
            margin-top: 30px;
            margin-bottom: 20px;
            padding-left: 30px;
            padding-right: 30px;

            .tab {
                margin-bottom: 6.5px;
                padding: 12px;
                width: 48%;
                background-color: #f5f7fb;
                color: #7d93b2;
                border-radius: 4px;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 12px;
                font-weight: 600;

                i {
                    margin-right: 10px;
                    font-size: 14px;
                }

                &.active {
                    background-color: #3270fc;
                    color: #fff;
                }
            }
        }

        .form {
            display: none;
            margin-top: 10px;
            padding-left: 30px;
            padding-right: 30px;

            &.show {
                display: block;
            }

            .group {
                display: flex;
                flex-direction: column;

                label {
                    margin-bottom: 10px;
                    color: #878c9f;
                    font-size: 12px;
                    font-weight: 500;
                }

                input {
                    margin-bottom: 20px;
                    padding: 15px 20px;
                    background-color: #f5f7fb;
                    color: #7d93b2;
                    font-size: 12px;
                    line-height: 14px;
                    border: 1px solid #e5e7f2;
                    border-radius: 4px;
                }
            }

            .btn-submit {
                margin-top: 5px;
                padding: 13px 40px;
                background-color: #3270fc;
                color: #fff;
                font-weight: 500;
                border: none;
                border-radius: 4px;
            }
        }

        .login-form {
            .actions {
                display: flex;
                justify-content: space-between;
                margin-bottom: 20px;

                .remember-me-box {
                    display: flex;
                    justify-content: flex-start;
                    align-items: center;
                    position: relative;

                    #remember-me {
                        appearance: none;
                        margin-right: 10px;
                        width: 20px;
                        height: 20px;
                        border: 1px solid #e5e7f2;
                        border-radius: 2px;

                        &:checked + i {
                            display: block;
                        }
                    }

                    i {
                        display: none;
                        position: absolute;
                        top: 50%;
                        left: 10px;
                        transform: translate(-50%, -50%);
                        color: #3270fc;
                    }

                    label {
                        margin-bottom: 0;
                        color: #7d93b2;
                        font-size: 11px;
                        font-weight: 600;
                    }
                }

                .lost-your-password {
                    a {
                        color: #7d93b2;
                        font-size: 12px;
                        font-weight: 600;
                    }
                }
            }
        }
    }
}
